body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #040429;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.box {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 6px;
  background: #ddd;
  width: 400px;
  height: 300px;
}
.box-item {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  align-items:flex-end;
}
.box-item button {
  padding: 10px 20px;
  margin: 30px 10px;
  border-radius: 6px;
  font-size: 22px;
  background: transparent;
  border: none;
}
#adder {
  border: 2px solid #0ed30e;
  color: #0ed30e;
}
#adder:hover {
  cursor: pointer;
  transition: 0.5s ease;
  transform: scale(1.1);
}
#minus {
  border: 2px solid #c5c50b;
  color: #c5c50b;
}
#minus:hover {
  cursor: pointer;
  transition: 0.5s ease;
  transform: scale(1.1);
}

#reset {
  border: 2px solid #f3600c;
  color: #f3600c;
}
#reset:hover {
  cursor: pointer;
  transition: 0.5s ease;
  transform: scale(1.1);
}
.number {
  font-size: 100px;
  position: absolute;
  width: 100%;
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  top: 20%;
}

@media screen and (max-width: 424px) {
  .box {
    width: 300px;
    height: 300px;
  }
}
@media screen and (max-width: 300px) {
  .box {
    width: 250px;
    height: 300px;
  }
  .box-item button {
    margin: 30px 5px;
  }
}